<template>

    <div v-if="curVue == 'Menu'" class="ur-morpion-menu">
        <h1 class="ur-mp-menu-title">Morpion</h1>
        <div class="ur-mp-menu-bt-container">
            <button type="button" class="ur-mp-menu-button ur-mp-1vs1" @click="launch_game('1vs1')">1 VS 1</button>
            <button type="button" class="ur-mp-menu-button ur-mp-1vsai" @click="launch_game('1vsai')">1 VS AI</button>
        </div>
    </div>

    <div v-else-if="curVue == 'Morpion'" class="ur-morpion-game">
        <div class="ur-mp-info-bar">
            <button type="button" class="ur-mp-back-but" @click="curVue = 'Menu'">Back</button>
            <div class="ur-mp-stats">
                <p class="title-player">Player 1</p>
                <p class="score-player score-p1">{{score[0]}}</p>
                <p class="score-separator">-</p>
                <p class="score-player score-p2">{{score[1]}}</p>
                <p class="title-player">Player 2</p>
            </div>
        </div>
        <div class="ur-mp-morpion">
            <div class="ur-mp-circle ur-mp-cross" style="display: none;"></div>
            <table>
                <tbody>
                <tr>
                    <td class="carre zonea1" @click="play('zonea1')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zonea2" @click="play('zonea2')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zonea3" @click="play('zonea3')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                </tr>
                <tr>
                    <td class="carre zoneb1" @click="play('zoneb1')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zoneb2" @click="play('zoneb2')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zoneb3" @click="play('zoneb3')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                </tr>
                <tr>
                    <td class="carre zonec1" @click="play('zonec1')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zonec2" @click="play('zonec2')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                    <td class="carre zonec3" @click="play('zonec3')">
                        <div class="ur-mp-circle invisible"></div>
                        <div class="ur-mp-cross invisible"></div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>

    <div v-else-if="curVue == 'Replay'" class="ur-morpion-replay">
        <h1 class="ur-mp-replay-title">{{winner}}</h1>
        <div class="ur-mp-stats">
            <p class="title-player">Player 1</p>
            <p class="score-player score-p1">{{score[0]}}</p>
            <p class="score-separator">-</p>
            <p class="score-player score-p2">{{score[1]}}</p>
            <p class="title-player">Player 2</p>
        </div>
        <div class="ur-mp-menu-bt-container">
            <button type="button" class="ur-mp-menu-button ur-mp-menu" @click="curVue = 'Menu'">Menu</button>
            <button type="button" class="ur-mp-menu-button ur-mp-replay" @click="curVue = 'Morpion'">Replay</button>
        </div>
    </div>

</template>

<script>
  export default {
    name: 'Morpion',
    data () {
      return {
        type: [],
        curVue: 'Menu',
        players: [],
        curPlayer: 1,
        winner: '',
        score: [0, 0],
        vsai: false
      }
    },
    methods: {
      launch_game: function(type) {
        this.curVue = 'Morpion'
        if (type === '1vs1')
          this.init1vs1()
        else
          this.init1vsai()
      },
      init1vs1: function() {
        this.score = [0, 0]
        this.vsai = false
        this.curPlayer = 1
      },
      init1vsai: function() {
        this.score = [0, 0]
        this.vsai = true
        this.curPlayer = 1
      },
      initReplay: function() {
        this.curVue = 'Replay'
        this.curPlayer = 1
      },
      play: function(zone) {
        let el = document.getElementsByClassName(zone)
        el = el.length === 1 ? el[0] : null

        if (el !== null && !el.classList.contains('played1') &&
          !el.classList.contains('played2')) {
          if (this.curPlayer === 1) {
            el.classList.add('played1')
            el.classList.add('played')
            let circleEl = el.getElementsByClassName('ur-mp-circle')[0]
            circleEl.classList.remove('invisible')
            // el.style.backgroundImage = 'url(../assets/image-morpion/circle-w.png)'
            this.curPlayer = 2
          } else if (this.curPlayer === 2) {
            el.classList.add('played2')
            el.classList.add('played')
            let crossEl = el.getElementsByClassName('ur-mp-cross')[0]
            crossEl.classList.remove('invisible')
            // el.style.backgroundImage = 'url(../assets/image-morpion/cross-w.png)'
            this.curPlayer = 1
          }

          if (!this.checkEnd()) {
            if (this.vsai && this.curPlayer === 2)
              this.aiplay()
          }
        }
      },
      aiplay: function() {
        let zones = [[document.getElementsByClassName('zonea1')[0], document.getElementsByClassName('zonea2')[0], document.getElementsByClassName('zonea3')[0]],
                     [document.getElementsByClassName('zoneb1')[0], document.getElementsByClassName('zoneb2')[0], document.getElementsByClassName('zoneb3')[0]],
                     [document.getElementsByClassName('zonec1')[0], document.getElementsByClassName('zonec2')[0], document.getElementsByClassName('zonec3')[0]]]
        let zonesClass = [['zonea1', 'zonea2', 'zonea3'],
                          ['zoneb1', 'zoneb2', 'zoneb3'],
                          ['zonec1', 'zonec2', 'zonec3']]

        let check = false
        let x = 0
        let y = 0
        while (!check) {
          x = Math.floor(Math.random() * Math.floor(3))
          y = Math.floor(Math.random() * Math.floor(3))

          if (!zones[y][x].classList.contains('played'))
            check = true
        }

        this.play(zonesClass[y][x])
      },
      checkEnd: function() {
        let zonea1 = document.getElementsByClassName('zonea1')[0]
        let zonea2 = document.getElementsByClassName('zonea2')[0]
        let zonea3 = document.getElementsByClassName('zonea3')[0]
        let zoneb1 = document.getElementsByClassName('zoneb1')[0]
        let zoneb2 = document.getElementsByClassName('zoneb2')[0]
        let zoneb3 = document.getElementsByClassName('zoneb3')[0]
        let zonec1 = document.getElementsByClassName('zonec1')[0]
        let zonec2 = document.getElementsByClassName('zonec2')[0]
        let zonec3 = document.getElementsByClassName('zonec3')[0]

        if (zonea1.classList.contains('played1') && zonea2.classList.contains('played1') && zonea3.classList.contains('played1') ||
          zoneb1.classList.contains('played1') && zoneb2.classList.contains('played1') && zoneb3.classList.contains('played1') ||
          zonec1.classList.contains('played1') && zonec2.classList.contains('played1') && zonec3.classList.contains('played1') ||
          zonea1.classList.contains('played1') && zoneb1.classList.contains('played1') && zonec1.classList.contains('played1') ||
          zonea2.classList.contains('played1') && zoneb2.classList.contains('played1') && zonec2.classList.contains('played1') ||
          zonea3.classList.contains('played1') && zoneb3.classList.contains('played1') && zonec3.classList.contains('played1') ||
          zonea1.classList.contains('played1') && zoneb2.classList.contains('played1') && zonec3.classList.contains('played1') ||
          zonea3.classList.contains('played1') && zoneb2.classList.contains('played1') && zonec1.classList.contains('played1')) {
          this.winner = 'Player 1 wins !'
          this.score[0]++
          this.initReplay()
          return true
        } else if (zonea1.classList.contains('played2') && zonea2.classList.contains('played2') && zonea3.classList.contains('played2') ||
          zoneb1.classList.contains('played2') && zoneb2.classList.contains('played2') && zoneb3.classList.contains('played2') ||
          zonec1.classList.contains('played2') && zonec2.classList.contains('played2') && zonec3.classList.contains('played2') ||
          zonea1.classList.contains('played2') && zoneb1.classList.contains('played2') && zonec1.classList.contains('played2') ||
          zonea2.classList.contains('played2') && zoneb2.classList.contains('played2') && zonec2.classList.contains('played2') ||
          zonea3.classList.contains('played2') && zoneb3.classList.contains('played2') && zonec3.classList.contains('played2') ||
          zonea1.classList.contains('played2') && zoneb2.classList.contains('played2') && zonec3.classList.contains('played2') ||
          zonea3.classList.contains('played2') && zoneb2.classList.contains('played2') && zonec1.classList.contains('played2')) {
          this.winner = 'Player 2 wins !'
          this.score[1]++
          this.initReplay()
          return true
        } else if (zonea1.classList.contains('played') && zonea2.classList.contains('played') && zonea3.classList.contains('played') &&
          zoneb1.classList.contains('played') && zoneb2.classList.contains('played') && zoneb3.classList.contains('played') &&
          zonec1.classList.contains('played') && zonec2.classList.contains('played') && zonec3.classList.contains('played')) {
          this.winner = 'Draw !'
          this.initReplay()
          return true
        }
        return false
      }
    }
  }
</script>

